import React,{Component} from 'react';
import './App.css';
import Dashboard from './components/_dashboard/dashboard';
import Login from './components/_login_signup/login'

import {BrowserRouter as Router,Switch,Route} from 'react-router-dom';

import About from './components/about';
import Signup from './components/_login_signup/signup'
import Navbar from './components/navbar';
import Footer from './components/footer'
import SideNavbar from './components/sidenavbar';


const routeItems = [
  {
    path: '/Link1',
    component: Dashboard

},
{
  path:'/About',
  component:About
},
{
  path:'/Login',
  component:Login
},
{
  path:'/Signup',
  component:Signup
}
]

export default class App extends Component {
  render(){
  return (
    
    <Router>
       <Navbar/>
         <Dashboard/>
         
     
       
          <Switch>
          { routeItems.map((item,index) => (
             <Route key={index} path={item.path} exact strict component={item.component}/>

            ))
           }
              </Switch>
          <Footer/>
      </Router>
  );
}
}
